import React, { Component } from "react";
import {
  Form,
  Button,
  Container,
  Row,
  Col,
  Alert,
  Spinner,
} from "react-bootstrap";
const queryString = require("query-string");

export default class Instagram extends Component {
  state = {
    username: "",
    password: "",
    showAlert: false,
    loading: false,
    message: "",
    twoFactor: false,
    success: false,
    code: "",
    two_factor_identifier: "",
    verificationMethod: "",
  };
  handleUsername = (event) => {
    this.setState({ username: event.target.value });
  };
  handleCode = (event) => {
    const { value, maxLength } = event.target;
    const message = value.slice(0, maxLength);
    this.setState({ code: message });
  };
  handlePassword = (event) => {
    this.setState({ password: event.target.value });
  };
  onShowAlert = () => {
    this.setState({ showAlert: true }, () => {
      window.setTimeout(() => {
        this.setState({ showAlert: false });
      }, 3000);
    });
  };
  handleClick = (e) => {
    e.preventDefault();
    this.setState({ loading: true });
    fetch("https://mysterious-reaches-98129.herokuapp.com/insta", {
      method: "POST",
      headers: {
        "Content-Type": "application/x-www-form-urlencoded;charset=UTF-8",
      },
      body: queryString.stringify(this.state),
    })
      .then((response) => response.json())
      .then((responseData) => {
        console.log(responseData);
        this.setState(
          {
            message: responseData.message,
          },
          () => {
            this.onShowAlert();
          }
        );
        if (!responseData.success) {
          this.setState({ loading: false });
        } else {
          if (responseData.twoFactor) {
            this.setState({
              twoFactor: true,
              loading: true,
              two_factor_identifier: responseData.two_factor_identifier,
              verificationMethod: responseData.verificationMethod,
            });
          } else {
            this.setState({ success: true });
          }
        }
      });
  };
  handleCodeClick = (e) => {
    if (this.state.code.length === 0 || this.state.code.length > 6) {
      this.setState({ message: "Code length should be equal to 6" }, () => {
        this.onShowAlert();
      });
    } else {
      fetch(
        `https://mysterious-reaches-98129.herokuapp.com/insta/submitCode?username=${this.state.username}&code=${this.state.code}&two_factor_identifier=${this.state.two_factor_identifier}&verificationMethod=${this.state.verificationMethod}`,
        {
          method: "GET",
        }
      )
        .then((response) => response.json())
        .then((responseData) => {
          console.log(responseData);
          if (responseData.success) {
            this.setState({ success: true });
          } else {
            this.setState({ message: responseData.message }, () => {
              this.onShowAlert();
            });
          }
        });
    }
  };
  handleLogoutClick = (e) => {
    fetch("https://mysterious-reaches-98129.herokuapp.com/logout/insta", {
      method: "GET",
    })
      .then((response) => response.json())
      .then((responseData) => {
        console.log(responseData);
        if (responseData.success) {
          this.setState({
            username: "",
            password: "",
            showAlert: false,
            loading: false,
            message: "",
            twoFactor: false,
            success: false,
          });
        }
      });
  };
  render() {
    return (
      <div>
        {this.state.showAlert ? (
          <Alert variant="danger">{this.state.message}</Alert>
        ) : (
          ""
        )}
        <Container
          style={{
            position: "absolute",
            left: "50%",
            top: "50%",
            transform: "translate(-50%, -50%)",
          }}
        >
          {this.state.success ? (
            <div>
              <h1>Login Sucess</h1>
              <h3>please check console</h3>
              <Button
                variant="warning"
                onClick={() => {
                  this.handleLogoutClick();
                }}
              >
                Logout
              </Button>
            </div>
          ) : (
            <Row>
              <Col>
                <Form onSubmit={(e) => this.handleClick(e)}>
                  <Form.Group controlId="formBasicEmail">
                    <Form.Label>Username</Form.Label>
                    <Form.Control
                      value={this.state.username}
                      onChange={(e) => {
                        this.handleUsername(e);
                      }}
                      type="username"
                      placeholder="Enter username"
                    />
                    <Form.Text className="text-muted">
                      We'll never share your email with anyone else.
                    </Form.Text>
                  </Form.Group>

                  <Form.Group controlId="formBasicPassword">
                    <Form.Label>Password</Form.Label>
                    <Form.Control
                      onChange={(e) => this.handlePassword(e)}
                      value={this.state.password}
                      type="password"
                      placeholder="Password"
                    />
                  </Form.Group>
                  {this.state.loading ? (
                    <Button variant="primary" type="submit" disabled>
                      <Spinner
                        as="span"
                        animation="grow"
                        size="sm"
                        role="status"
                        aria-hidden="true"
                      />
                      Loading...
                    </Button>
                  ) : (
                    <Button variant="primary" type="submit">
                      Login
                    </Button>
                  )}

                  {this.state.twoFactor ? (
                    <div>
                      <Form.Group>
                        <Form.Label>Enter two factor code recieved</Form.Label>
                        <Form.Control
                          value={this.state.code}
                          onChange={(e) => {
                            this.handleCode(e);
                          }}
                          type="number"
                          maxLength="11"
                        />
                        <Form.Text className="text-muted">
                          Please enter the code used for two factor login!
                        </Form.Text>
                      </Form.Group>
                      <Button
                        variant="outline-success"
                        onClick={() => {
                          this.handleCodeClick();
                        }}
                      >
                        submit code
                      </Button>{" "}
                    </div>
                  ) : (
                    ""
                  )}
                </Form>
              </Col>
            </Row>
          )}
        </Container>
      </div>
    );
  }
}
